
    export default function meriactionminus(options={}){
        if(options.origin){
            return '<svg  width="'+(options.size || 16) +'" height="'+(options.size || 16) +'"   fill="none" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24"><path d="M.95 12.108a1 1 0 011-1h20a1 1 0 110 2h-20a1 1 0 01-1-1z" fill="#1B2129"></path></svg>'
        }
        return '<svg xmlns="http://www.w3.org/2000/svg" width="'+ (options.size || 16) +'" height="'+ (options.size || 16) +'" viewBox="0 0 24 24" aria-hidden="true" fill="'+ (options.color || "currentColor") +'"><path d="M.95 12.108a1 1 0 011-1h20a1 1 0 110 2h-20a1 1 0 01-1-1z"></path></svg>'
    }
